/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.mkdf-woo-single-page {
	
	&.mkdf-woo-single-thumb-on-left-side {
		
		.mkdf-single-product-content {
			
			.images {
				
				@media only screen and (min-width: $ipad-portrait-plus-pixel) {
					
					figure {
						width: 100%;
						margin: 0;
					}
					
					.woocommerce-product-gallery__image {
						
						&:first-child {
							left: 120px;
							width: calc(100% - 120px);
						}
					}
					
					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: 100px;
								height: calc(25% + 5px);
								padding: 0 0 20px;
								box-sizing: border-box;
								
								a {
									height: 100%;
									
									img {
										height: 100%;
									}
								}
							}
							
							$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
							@for $i from 0 to length($thumbnail_numbers) {
								$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
								
								&:nth-child(#{$thumbnail_number_value}#{n}) {
									$first_param: (25 * $i)#{'%'};
									$second_param: (5 * $i)#{px};
									$topPos: calc(#{$first_param} + #{$second_param});
									
									top: $topPos;
								}
							}
						}
					}
				}
			}
		}
	}
    
    .product {
	    @include mkdfRelativeHolderLayout();
	    float: none;
	    margin: 0 0 70px;
    }
        
    .mkdf-single-product-content {
        @include mkdfRelativeHolderLayout();
        clear: both;
        margin: 0 0 20px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 55%;
		    margin: 0;
		    padding: 0 29px 0 0;
		    float: left;
		    box-sizing: border-box;
		    
		    figure {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -10px;
			
			    a {
				    @include mkdfRelativeHolderLayout();
				
				    img {
					    position: relative;
					    width: 100%;
					    display: block;
				    }
			    }
		    }
		
		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }
		    
		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 10px;
			    box-sizing: border-box;
			    
			    &:first-child {
				    left: 10px;
				    width: calc(100% - 20px);
				    padding: 0;
			    }
			
			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    padding-top: 20px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
    .mkdf-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 45%;
        padding: 0 0 0 29px;
        float: left;
        box-sizing: border-box;
	
	    .mkdf-single-product-title {
		    display: block;
		    margin: 0 0 18px;
	    }
	
	    .price {
		    @include mkdfWooCommercePriceStyle();
			font-size: 30px;
			font-weight: 300;
			line-height: 0.87em;
            
			del{
                font-size: 17px;
                font-weight: 300;
			}
	    }
	
	    .woocommerce-product-rating {
		    @include mkdfRelativeHolderLayout();
		    line-height: 1;
		    margin: 13px 0 0;
			.star-rating{
				color: $first-main-color;
			}
	    }
	
	    .woocommerce-product-details__short-description {
		    @include mkdfRelativeHolderLayout();
		    margin: 44px 0 0;
		
		    p {
			    margin: 0;
		    }
	    }
	
	    .product_meta {
		    margin: 46px 0 0;
		
		    > span {
			    display: block;
			    margin: 0;
				color: $meta-color;
				font-size: 12px;
				font-family: $default-heading-font;
				font-weight: 400;
				letter-spacing: 0.2em;
				text-transform: uppercase;

				span, a{
					color: $default-text-color;
					font-size: 14px;
					font-weight: 300;
				}
			    a {
				    color: $default-text-color;
				
				    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
	    }
	
	    .mkdf-woo-social-share-holder {
		    @include mkdfRelativeHolderLayout();
		
		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }
		    > span {
			    padding: 0 9px 0 0;
		    }

			.mkdf-social-share-holder.mkdf-list {
				.mkdf-social-title {
					margin-right: 10px;
					color: $meta-color;
					font-size: 12px;
					font-family: $default-heading-font;
					font-weight: 400;
					letter-spacing: 0.2em;
					text-transform: uppercase;
				}

				li {
					margin-right: 17px;
				}
			}
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-heading-color;
		    font-weight: bold;
		    margin: 18px 0 0;
	    }
	
	    form.cart {
		    @include mkdfRelativeHolderLayout();
		    margin: 47px 0 0;
	    }
	
	    .mkdf-quantity-buttons {
		    margin: 0 20px 0 0;
			.mkdf-quantity-input{
				font-style: normal;
				color: $default-heading-color;
				font-size: 19px;
			}
			span{
				&.mkdf-quantity-plus,
				&.mkdf-quantity-minus{
					color: $default-heading-color;
					font-size: 18px;
				}
			}
	    }
    }
	
	.woocommerce-tabs {
		@include mkdfRelativeHolderLayout();
		margin: 43px 0 0;
		
		ul.tabs {
			@include mkdfRelativeHolderLayout();
			list-style: none;
			margin: 0;
			padding: 0;
			text-align: left;
			clear: both;
			font-size: 0;
			
			> li {
				position: relative;
				display: inline-block;
                margin-right: 0;
				vertical-align: middle;
				background-color: #fff;
				border: 1px solid $default-border-color;
				border-right-width:0;
				font-size: 16px;

				&:last-of-type{
					border-right-width: 1px;
				}

				&.active{
					background-color: $first-main-color;
					border-color: $first-main-color;
					a{
						color: #ffffff;
					}
				}
				
				a {
					position: relative;
					display: inline-block;
					vertical-align: top;
					padding: 13px 45px 12px;
					font-size: 16px;
					letter-spacing: 0.05em;
					text-transform: uppercase;
				}
			}
		}
		
		.entry-content {
			@include mkdfRelativeHolderLayout();
			box-sizing: border-box;
			padding: 0 30px;
			
			> h2 {
				display: none;
			}
			
			> p {
				margin: 20px 0;
			}
		}
		
		table {
			margin: 20px 0;
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;
				
				p {
					margin: 0;
				}
			}
			
			th {
				color: #9c9c9c;
				width: 1%;
				padding: 0 10px 0 0;
				font-size: 12px;
				font-family: $default-heading-font;
				font-weight: 400;
				letter-spacing: .2em;
				text-transform: uppercase;
				vertical-align: bottom;
			}
			
			td {
				width: 99%;
			}
		}
		
		#reviews {
			@include mkdfRelativeHolderLayout();
			padding: 20px 0;
			box-sizing: border-box;
			
			h2 {
				font-size: 30px;
				line-height: 24px;
				margin: 16px 0 32px;
			}
			
			ol.commentlist {
				list-style: none;
                margin-bottom: 24px;
				
				li {
					margin: 0 0 15px;
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include mkdfRelativeHolderLayout();
					
					img {
						float: left;
						width: 70px;
					}
				}
				
				.comment-text {
					padding: 0 0 0 90px;
					
					p {
						margin: 0;
					}
				}
			}
			
			.comment-respond {
				margin-top: 15px;
				
				.comment-reply-title {
					margin: 0 0 10px;
				}
				
				p {
					margin: 0;
				}
				
				.stars {
					overflow: hidden;
                    line-height: 26px;
                    margin-bottom: 1em;
					
					a {
						position: relative;
						display: inline-block;
						color: transparent;
						font-size: 0;
						margin: 0;
						@include mkdfWooCommerceSingleProductRatingsStyle();
					}
				}
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 30px 0 0;
		
		> h2 {
			display: block;
			font-size: 30px;
			line-height: 1.333em;
			text-align: left;
			margin: 80px 0 24px;
		}
		
		.product {
			
			.mkdf-pl-text-wrapper {
				margin-bottom: 0;
			}
			.star-rating{
				color: $first-main-color;
			}
		}
	}
}

.woocommerce-page .mkdf-content .button[type=submit].single_add_to_cart_button {
    vertical-align: top;
}

.single_add_to_cart_button {
	border: 1px solid transparent;
}

/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */