/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.mkdf-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    padding: 0 10px;
    box-sizing: border-box;

    .touch & {
        cursor: pointer;
    }

    .mkdf-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .mkdf-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;

        .mkdf-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;
        }

        .mkdf-cart-number {
            position: absolute;
            top: 50%;
            left: 50%;
            border-radius: 50%;
            background: $first-main-color;
            color: #fff;
            width:15px;
            height: 15px;
            font-size: 12px;
            line-height: 16px;
            text-align: center;

        }

        &.mkdf-header-cart-icon-pack {
            .mkdf-cart-icon {
                font-size: 20px;
                line-height: 1em;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        &.mkdf-header-cart-svg-path {
            .mkdf-cart-icon {

                >* {
                    vertical-align: middle;
                }
            }
        }
    }

    &:hover {

        .mkdf-shopping-cart-dropdown {
            opacity: 1;
            max-height: 100vh;
            visibility: visible;
        }
    }
}

.mkdf-dark-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.mkdf-light-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.mkdf-shopping-cart-dropdown {
    position: absolute;
    top: 100%;
    max-height: 0;
    overflow: hidden;
    left: auto;
    right: 0;
    width: 245px;
    background-color: #fff;
    text-align: left;
    opacity: 0;
    visibility: hidden;
    box-sizing: border-box;
    border-top: 2px solid $first-main-color;
    @include mkdfTransition(max-height 0.7s ease-out);
    z-index: 1000;

    ul {
        margin: 20px;
        padding: 0;

        li {
            @include mkdfTableLayout();
            padding: 0 0 10px;
            box-sizing: border-box;

            &.mkdf-empty-cart {
                color: initial;
                border-bottom: 0;
            }

            &:first-child {
                padding-top: 0;
            }

            &:last-child {
                padding-bottom: 0;
            }
        }
    }

    .mkdf-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 75px;
        vertical-align: top;

        img {
            display: block;
        }
    }

    .mkdf-item-info-holder {
        @include mkdfTableCellLayout();
        padding: 0 19px;

        .mkdf-product-title {
            display: block;
            margin: 0;
            box-sizing: border-box;
            font-size: 16px;
        }

        .remove {
            position: absolute;
            top: -1px;
            right: 0;
	        font-size: 18px;
	        line-height: 1;

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .mkdf-quantity {
            display: block;
            margin: 3px 0 0;
            font-size: 12px;
            line-height: 1.2em;
            letter-spacing: 0.05em;
        }

        .amount {
            display: block;
            margin: 0;
            color: $default-heading-color;
            font-size: 14px;
        }
    }

    .mkdf-cart-bottom {
        padding: 13px 0 0;

        .mkdf-subtotal-holder {
            position: relative;
            display: table;
            width: 100%;
            margin: 0 0 10px;

            > * {
                display: table-cell;
                vertical-align: top;
                color: $default-heading-color;
                line-height: 20px;
            }
            .mkdf-total{
                font-size: 15px;
                letter-spacing: 0.05em;
            }

            .mkdf-total-amount {
                text-align: right;
                font-family: $default-heading-font;
	            font-size: 22px;
            }
        }

        .mkdf-view-cart {
            @include mkdfRelativeHolderLayout();
            height: 52px;
            line-height: 52px;
            padding: 0 26px;
	        letter-spacing: .2em;
            font-family: $default-heading-font;
            @include mkdfButtonSolidColor();
            text-align: center;
            box-sizing: border-box;
            @include mkdfTransition(color .2s ease-out, background-color .2s ease-out);

            span {
                display: block;
            }

            &:hover {
                @include mkdfButtonSolidOverlayHoverColor();
            }
        }
    }
}

.mkdf-vertical-menu-area {

    .mkdf-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */