/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin mkdfWooCommerceMarksGeneralStyle() {
    position: absolute;
    display: block;
    top: 30px;
    width: auto;
    background: $first-main-color;
    color: #fff;
    font-size: 14px;
    line-height: 24px;
    letter-spacing: 1px;
    text-align: center;
    box-sizing: border-box;
    margin: 0;
    padding: 6px 35px;
    z-index: 100;
    @include mkdfTransform(translateZ(0));
}

@mixin mkdfWooCommerceSaleStyle() {
    right: 0;

	&:before{
		border-color: transparent $first-main-color transparent transparent;
	}
}

@mixin mkdfWooCommerceOutOfStockStyle() {
    right: 0;

	&:before{
		border-color: transparent $default-heading-color transparent transparent;
	}
}

@mixin mkdfWooCommerceNewProductStyle() {
    right: 0;

	&:before{
		border-color: transparent $first-main-color transparent transparent;
	}
}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin mkdfWooCommercePriceStyle() {
    position: relative;
    display: block;
    margin: 8px 0;
    line-height: 1;
    color: $default-heading-color;
    
    * {
        font-size: inherit;
        line-height: inherit;
    }
    
    del {
        margin: 0 8px 0 0;
        color: rgba($default-heading-color, .8);
    }
    
    ins {
        text-decoration: none;
    }
}
/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: "ElegantIcons";
    font-size: 12px;
    line-height: 1;
    letter-spacing: 2px;
    color: $first-main-color;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin mkdfWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    @extend %star-ratings;
    overflow: hidden;
    
    &:before {
        content: "\e031\e031\e031\e031\e031";
    }
    
    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        white-space: nowrap;
        
        &:before {
            content: "\e033\e033\e033\e033\e033";
        }
    }
}

@mixin mkdfWooCommerceSingleProductRatingsStyle() {
    
    &:before {
        content: "\e031";
        position: relative;
        display: inline-block;
        vertical-align: middle;
        @extend %star-ratings;
        overflow: hidden;
        z-index: -1;
    }
    
    &.active:after {
        content: "\e033\e033\e033\e033\e033";
        position: absolute;
        right: 0;
        bottom: 7px;
        display: block;
        @extend %star-ratings;
    }
}
/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */
