/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.mkdf-pl-holder {
    @include mkdfRelativeHolderLayout();

	&.mkdf-info-on-image {

		.mkdf-pli {

			.mkdf-pli-add-to-cart {
				margin: 25px 0 0;
			}

            .added_to_cart {
	            @include mkdfButtonDefaultStyle();
	            white-space: nowrap;
	            z-index: 3;
            }

			&:hover{
				.mkdf-pli-text.mkdf-pli-hover-overlay {
					opacity: 1;
				}
			}

			a.button.added {
				display: none;
			}
		}

	}

    &.mkdf-info-below-image{

		.mkdf-circle-frame > .mkdf-pli-inner {
			border-radius: 50%;
		}

        .mkdf-pli {

            .mkdf-pli-price,
            .mkdf-pli-add-to-cart{
                @include mkdfDefaultTransition(color);
                @include mkdfTransition(all .35s cubic-bezier(0.02, 0.51, 0.71, 1.03));
            }
            .mkdf-pli-price {
                opacity: 1;
            }
            .mkdf-pli-add-to-cart {
                opacity: 0;
                position: absolute;
                @include mkdfTransform(translateY(100%));
                width: 100%;
            }

            &:hover {
				img{
					@include mkdfTransform(scale(1.07) translateZ(0));
				}

                .mkdf-pli-price {
                    opacity: 0;
                    @include mkdfTransform(translateY(-200%));
                }
                .mkdf-pli-add-to-cart {
                    opacity: 1;
                    @include mkdfTransform(translateY(-100%));
                }
            }

            .mkdf-pli-add-to-cart{
                @extend %meta-style;
				font-size: 14px;
				font-weight: 400;
				letter-spacing: 0.04em;

                a {
                    padding: 16px;

                    &:hover {
                        color: $first-main-color;
                    }
                }
            }

            a.button.added {
                display: none;
            }
        }
    }

    .mkdf-pli {
        position: relative;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        padding: 0;
        z-index: 100;
        box-sizing: border-box;

	    .mkdf-pli-title {
		    margin: 0 0 4px;
	    }

	    .mkdf-pli-category {
		    margin: 11px 0 3px;
	    }

        .mkdf-pli-excerpt {
            color: $default-heading-color;
	        line-height: 1.3em;
            margin: 11px 0 3px;
        }

	    .mkdf-pli-rating-holder {
		    @include mkdfRelativeHolderLayout();
		    line-height: 1;
		    margin: 7px 0 12px;
	    }

	    .mkdf-pli-rating {
		    @include mkdfWooCommerceRatingsStyle();
	    }


        .mkdf-pli-text-inner {
            position: relative;
            overflow: hidden;
        }

        .mkdf-pli-price {
	        @include mkdfWooCommercePriceStyle();
        }

    }

    .mkdf-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;

        &:hover {

            .mkdf-pli-text.mkdf-pli-button-from-bottom .mkdf-button,
            .mkdf-pli-text.mkdf-pli-button-from-bottom .added_to_cart{
        		@include mkdfTransform(translateY(0));
            }
        }

        .touch & {
            cursor: pointer;
        }

        .mkdf-pli-image {
            @include mkdfRelativeHolderLayout();
			overflow: hidden;

            img {
                display: block;
                width: 100%;
				@include mkdfTransform(scale(1) translateZ(0));
				@include mkdfTransitionTransform($default-transition-duration $default-easing-function);
            }

            .mkdf-pli-onsale,
            .mkdf-pli-out-of-stock,
            .mkdf-pli-new-product {
	            @include mkdfWooCommerceMarksGeneralStyle();
            }

            .mkdf-pli-onsale {
	            @include mkdfWooCommerceSaleStyle();
            }

            .mkdf-pli-out-of-stock {
	            @include mkdfWooCommerceOutOfStockStyle();
            }

	        .mkdf-pli-new-product {
		        @include mkdfWooCommerceNewProductStyle();
	        }
        }

        .mkdf-pli-text {
            @include mkdfAbsoluteHolderLayout();
            box-sizing: border-box;

			.mkdf-pli-title {
				font-size: 24px;
				margin: 1em 0 4px;
			}

            .mkdf-button,
        	.added_to_cart{
        		white-space: normal;
        	}

            &.mkdf-pli-hover-overlay{
            	padding: 10px 15px;
	            opacity: 0;
	            background-color: rgba($default-heading-color, 0.4);
                @include mkdfDefaultTransition(opacity);
            }

            &.mkdf-pli-button-from-bottom{

            	.mkdf-pli-text-inner{
            		vertical-align: bottom;
            	}

            	.mkdf-button,
            	.added_to_cart{
            		width: 100%;
            		padding-left: 10px;
            		padding-right: 10px;
            		@include mkdfTransform(translateY(100%));
            		@include mkdfTransitionTransform($default-transition-duration $default-easing-function);
            	}
            }
        }

        .mkdf-pli-text-outer {
	        @include mkdfTableLayout();
	        text-align: center;
        }

        .mkdf-pli-text-inner {
            @include mkdfTableCellLayout();

            .mkdf-pli-title,
            .mkdf-pli-category a,
            .mkdf-pli-excerpt,
            .mkdf-pli-rating,
            .mkdf-pli-price {
                color: #fff;
            }

            .mkdf-pli-price del{
		        color: rgba(#fff, .8);
            }

	        .mkdf-pli-category a:hover {
		        color: rgba(#fff, .8);
	        }

	        .mkdf-pli-add-to-cart {

		        &.mkdf-default-skin {

			        .button,
                    .added_to_cart
                    {
				        @include mkdfButtonSolidColor();
						@include mkdfButtonSmallParams();
                        max-width: 240px;
						border: none;

				        &:hover {
							@include mkdfButtonSolidOverlayHoverColor();
				        }
			        }
		        }

		        &.mkdf-light-skin {

			        .button,
			        .added_to_cart {
				        color: $default-heading-color;
				        background-color: #fff;

				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }

		        &.mkdf-dark-skin {

			        .button,
			        .added_to_cart {
				        color: #fff;
				        background-color: $default-heading-color;

				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }
	        }

            .button {
	            @include mkdfButtonDefaultStyle();
	            @include mkdfButtonMediumParams();
	            white-space: nowrap;
	            z-index: 3;
            }

        }

	    .mkdf-pli-link {
		    @include mkdfAbsoluteHolderLayout();
	    }
    }

    .mkdf-pli-text-wrapper {
        @include mkdfRelativeHolderLayout();
        margin: 22px 0 38px;
        text-align: center;
    }

	&.mkdf-product-info-light {

		.mkdf-pli-inner {

			.mkdf-pli-text-inner {

				.mkdf-pli-title,
				.mkdf-pli-category a,
				.mkdf-pli-excerpt,
				.mkdf-pli-rating,
				.mkdf-pli-price {
					color: #fff;
				}

				.mkdf-pli-category a:hover {
					color: rgba(#fff, .8);
				}
			}
		}
	}

	&.mkdf-product-info-dark {

		.mkdf-pli-inner {

			.mkdf-pli-text-inner {

				.mkdf-pli-title,
				.mkdf-pli-category a,
				.mkdf-pli-excerpt,
				.mkdf-pli-rating,
				.mkdf-pli-price {
					color: $default-heading-color;
				}

				.mkdf-pli-category a:hover {
					color: rgba($default-heading-color, .8);
				}
			}
		}
	}

	/*************** Standard Layout - begin ***************/

	&.mkdf-standard-layout {

		.mkdf-pli {
			float: left;
		}

		&.mkdf-large-space {
			margin: 0 0 -50px;
		}

		&.mkdf-normal-space {
			margin: 0 0 -30px;
		}

		&.mkdf-small-space {
			margin: 0 0 -20px;
		}

		&.mkdf-tiny-space {
			margin: 0 0 -10px;
		}

		$standard_list_columns: ('two', 'three', 'four', 'five', 'six');

		@for $i from 0 to length($standard_list_columns) {
			&.mkdf-#{nth($standard_list_columns,$i+1)}-columns {
				$column_width: 100%/($i+2);

				.mkdf-pli {
					width: $column_width;

					@media only screen and (min-width: $ipad-landscape-plus-pixel) {

						&:nth-child(#{$i+2}n+1) {
							clear: both;
						}
					}
				}
			}
		}
	}

	/*************** Standard Layout - end ***************/

	/*************** Masonry Layout - begin ***************/

	&.mkdf-masonry-layout {

		.mkdf-pl-outer {
			opacity: 0;
		}

		.mkdf-pl-sizer {
			width: 100%;
		}

		.mkdf-pl-gutter {
			width: 0;
		}

		.mkdf-pli-inner {
			width: 100%;
			height: 100%;

			.mkdf-pli-image {
				height: 100%;

				img {
					height: 100%;
				}
			}
		}

		.mkdf-pli.mkdf-woo-image-large-width {

			img {
				width: 100%;
			}
		}

		$masonry_list_columns: ('two', 'three', 'four', 'five', 'six');

		@for $i from 0 to length($masonry_list_columns) {
			&.mkdf-#{nth($masonry_list_columns,$i+1)}-columns {
				$column_width: 100%/($i+2);

				.mkdf-pli,
				.mkdf-pl-sizer {
					width: $column_width;
				}

				.mkdf-pli.mkdf-woo-image-large-width,
				.mkdf-pli.mkdf-woo-image-large-width-height {
					width: $column_width*2;
				}
			}
		}
	}

	/*************** Masonry Layout - end ***************/
}
/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */