/* ==========================================================================
   WooCommerce Product List page styles - begin
   ========================================================================== */

.mkdf-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells,
.mkdf-content .woocommerce {
    @include mkdfRelativeHolderLayout();
}

.mkdf-pl-main-holder {
    margin: 0 0 22px;
    
    .mkdf-woo-pl-info-on-image-hover & {
        margin: 0 0 50px;
    }
}

ul.products {
    list-style: none;
	margin: 0 -15px;
    clear: both;
    
    > .product {
        position: relative;
        display: inline-block;
	    vertical-align: middle;
	    float: left;
	    margin: 0 0 30px;
	    padding: 0 15px;
	    box-sizing: border-box;
        
        .touch & {
            cursor: pointer;
        }
        
        .button,
        .added_to_cart {
            
            .mkdf-woo-pl-info-below-image & {
                position: absolute;
                left: 0;
                width: 100%;
                @extend %meta-style;
                font-size: 14px;
                font-weight: 400;
                letter-spacing: 0.04em;
                background-color: transparent;
                margin: 0;
                padding: 16px;
                opacity: 0;
                box-sizing: border-box;
                @include mkdfDefaultTransition(color);
                @include mkdfTransform(translateY(100%));
                @include mkdfTransition(all .35s cubic-bezier(0.02, 0.51, 0.71, 1.03));
                
                &:hover{
                    color: $first-main-color;
                    background-color: transparent;
                    border:none;
                }

                &:after{
                    display: none;
                }
            }
            
            .mkdf-woo-pl-info-on-image-hover & {
                padding: 16px;
                width: 100%;
                max-width: 240px;
                @include mkdfButtonSolidColor();
                border: none;

                &:hover{
                    color: #fff;
                    background-color: $first-main-color;
                    @include mkdfButtonSolidOverlayHoverColor();
                }
            }
            
        
        }
        
        &:hover {

            .mkdf-woo-pl-info-on-image-hover & {
                .mkdf-pl-inner .mkdf-pl-text.mkdf-pl-hover-overlay {
                    opacity: 1;
                }
            }
            
            .mkdf-woo-pl-info-below-image & {
                .price {
                    @include mkdfTransform(translateY(-200%));
                    opacity: 0;
                }

                .button,
                .added_to_cart {
                    @include mkdfTransform(translateY(-80%));
                    opacity:1;
                    border:none;
                }

                .mkdf-pl-inner {
                    .mkdf-pl-image {
                        img {
                            @include mkdfTransform(scale(1.07) translateZ(0));
                        }
                    }
                }
                
            }
        }
        
        .mkdf-product-list-title {
            margin: 0;
        }
        
        .mkdf-pl-rating-holder {
            @include mkdfRelativeHolderLayout();
            line-height: 1;
            margin: 6px 0 9px;

            .star-rating {
                color: $first-main-color;
            }
        }
        
        .mkdf-pli-text-inner {
            position: relative;
            overflow: hidden;
        }
        
        .price {
            @include mkdfWooCommercePriceStyle();
            
            opacity: 1;
            @include mkdfTransition(all .35s cubic-bezier(0.02, 0.51, 0.71, 1.03));
        }
        
        .mkdf-pl-inner {
            @include mkdfRelativeHolderLayout();
            overflow: hidden;
            
            .mkdf-pl-image {
                @include mkdfRelativeHolderLayout();
                overflow: hidden;
                
                img {
                    display: block;
                    width: 100%;
                    @include mkdfTransform(scale(1) translateZ(0));
                    @include mkdfTransitionTransform($default-transition-duration $default-easing-function);
                }
            }
            
            .mkdf-pl-text {
                @include mkdfAbsoluteHolderLayout();
                box-sizing: border-box;

                .mkdf-pl-btn,
            	.added_to_cart{
            		white-space: normal;
            	}

                &.mkdf-pl-hover-overlay{
                	padding: 10px 15px;
		            opacity: 0;
		            background-color: rgba($default-heading-color, 0.4);
	                @include mkdfDefaultTransition(opacity);
                }

                &.mkdf-pl-button-from-bottom{

                	.mkdf-pl-text-inner{
                		vertical-align: bottom;
                	}

                	.mkdf-pl-btn,
                	.added_to_cart{
                		width: 100%;
                		padding-left: 10px;
                		padding-right: 10px;
                		@include mkdfTransform(translateY(100%));
                		@include mkdfTransitionTransform($default-transition-duration $default-easing-function);
                	}
                }
            }
            
            .mkdf-pl-text-outer {
                @include mkdfTableLayout();
                height: 101%; //because of the partial px
                width: 101%; //because of the partial px
                text-align: center;
            }
            
            .mkdf-pl-text-inner {
                @include mkdfTableCellLayout();
                
                .mkdf-product-list-title,
                .price,
                .star-rating {
                    color: #fff;
                }
                
                .price del {
                    color: rgba(#fff, .8);
                }
            }
            
            > a {
                @include mkdfAbsoluteHolderLayout();
            }
        }
        
        .mkdf-pl-text-wrapper {
            @include mkdfRelativeHolderLayout();
            text-align: center;
            margin: 24px 0 38px;
        }
    }
}


$columns_space_label: ('no', 'tiny', 'small', 'normal', 'medium', 'large');
$columns_space_width: (0, 5, 10, 15, 20, 25);

@for $i from 0 to length($columns_space_label) {
    &.mkdf-woo-#{nth($columns_space_label,$i+1)}-space {
        $column_width: nth($columns_space_width,$i+1);
        $column_width_margin: $column_width * 2;
        
        ul.products {
            @if($column_width == 0) {
                margin: 0;
            } @else {
                margin: 0 -#{$column_width}px;
            }
            
            > .product {
                padding: 0 #{$column_width}px;
                margin: 0 0 #{$column_width_margin}px;
            }
        }
    }
}

.mkdf-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells {
    
    $product_list_columns: ('columns-3', 'columns-4');
    
    @for $i from 0 to length($product_list_columns) {
        $column_width: 100%/($i+3);
        
        > .products {
            
            > .product {
                
                @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                    
                    .mkdf-woocommerce-#{nth($product_list_columns,$i+1)} & {
                        width: $column_width;
                        
                        &:nth-child(#{$i+3}n+1) {
                            clear: both;
                        }
                    }
                }
            }
        }
    }
}

.woocommerce {
    
    &.columns-1 {
        
        > .products {
            
            > .product {
                float: none;
                width: 100%;
            }
        }
    }
    
    $product_list_vc_columns: ('columns-2', 'columns-3', 'columns-4', 'columns-5', 'columns-6');
    
    @for $i from 0 to length($product_list_vc_columns) {
        &.#{nth($product_list_vc_columns,$i+1)} {
            $column_vc_width: 100%/($i+2);
            
            > .products {
                
                > .product {
                    width: $column_vc_width;
                    $product_list_vc_columns_label: #{nth($product_list_vc_columns,$i+1)};
                    
                    @if $product_list_vc_columns_label == 'columns-5' {
                        @media only screen and (min-width: $laptop-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-6' {
                        @media only screen and (min-width: $laptop-landscape-large-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-3' {
                        @media only screen and (min-width: $ipad-portrait-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-2' {
                        @media only screen and (min-width: $phone-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else {
                        @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Product List page styles - end
   ========================================================================== */